import React, { useState } from 'react'
import { FeaturedCard, FolderCard, MotionDiv } from '../../components'

const Work = React.memo(({ featuredProjects, archiveProjects }) => {
  const [showMore, setShowMore] = useState(false)

  return (
    <MotionDiv>
      <h1 className='title'>My Projects</h1>
      <div className='featured-container'>
        {featuredProjects.map((featuredProject, index) => (
          <FeaturedCard
            key={featuredProject.id}
            data={featuredProject}
            reverse={index % 2 === 1}
          />
        ))}
      </div>
      <h2 className='sub-title'>Other Projects</h2>
      <div className='folders'>
        {showMore
          ? archiveProjects.map((archiveProject) => (
              <FolderCard key={archiveProject.id} data={archiveProject} />
            ))
          : archiveProjects
              .slice(0, 6)
              .map((archiveProject) => (
                <FolderCard key={archiveProject.id} data={archiveProject} />
              ))}
      </div>
      {archiveProjects.length > 6 && (
        <div className='more'>
          <div onClick={() => setShowMore(!showMore)} className='btn-small'>
            {showMore ? 'Show Less' : 'Show More'}
          </div>
        </div>
      )}
    </MotionDiv>
  )
})

export default Work
