import { collection, onSnapshot } from 'firebase/firestore'
import { AnimatePresence } from 'framer-motion'
import { useEffect, useState } from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import { Footer, Navbar } from '../components'
import db from '../config'
import { About, Contact, Home, NotFound, Splash, Work } from '../pages'

const Router = () => {
  const [featuredProjects, setFeaturedProjects] = useState([])
  const [archiveProjects, setArchiveProjects] = useState([])
  const [showSplash, setShowSplash] = useState(false)
  const [isLoading, setIsLoading] = useState(true)

  const snapshotData = () =>
    onSnapshot(
      collection(db, 'projects'),
      { includeMetadataChanges: true },
      (snapshot) => {
        const data = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }))
        setFeaturedProjects(data.filter((e) => e.featured).slice(0, 3))
        setArchiveProjects(data.filter((e) => !e.featured))

        setShowSplash(true)
        setTimeout(() => {
          setIsLoading(false)
        }, 3200)
      }
    )

  useEffect(snapshotData, [])

  if (isLoading) {
    return <AnimatePresence>{showSplash ? <Splash /> : null}</AnimatePresence>
  }

  return (
    <BrowserRouter>
      <Navbar />
      <main>
        <AnimatePresence>
          <Routes>
            <Route index element={<Home />} />
            <Route path='/about' element={<About />} />
            <Route
              path='/work'
              element={
                <Work
                  featuredProjects={featuredProjects}
                  archiveProjects={archiveProjects}
                />
              }
            />
            <Route path='/contact' element={<Contact />} />
            <Route path='*' element={<NotFound />} />
          </Routes>
        </AnimatePresence>
      </main>
      <Footer />
    </BrowserRouter>
  )
}

export default Router
